import 'flatpickr/dist/flatpickr.css';

export default {
  congratulatoryOfficerName: 'Tablet2',
  mixins: [myMixin],
  components: {
    Footer,
    BaekiltalsangchukKor,
    BaekiltalsangchukMan,
    BalingosaKor,
    BalingosaMan,
    BanhonjechukKor,
    BanhonjechukMan,
    Chohon,
    ChungugosaKor,
    ChungugosaMan,
    DaesangchukKor,
    DaesangchukMan,
    DangiltalgsangchukKor,
    DangiltalgsangchukMan,
    DongkangsunyoungchukKor,
    DongkangsunyoungchukMan,
    GumyochukKor,
    GumyochukMan,
    GyebinchukKor,
    GyebinchukMan,
    HwajangjechukKor,
    HwajangjechukMan,
    JaewoochukKor,
    JaewoochukMan,
    JeonyukjesachukKor,
    JeonyukjesachukMan,
    NapgolmyobonganchukKor,
    NapgolmyobonganchukMan,
    NojechukKor,
    NojechukMan,
    PamyochukKor,
    PamyochukMan,
    PyungtochukKor,
    PyungtochukMan,
    SamutalsangchukKor,
    SamutalsangchukMan,
    SamwoojechukKor,
    SamwoojechukMan,
    SangsinjechukKor,
    SangsinjechukMan,
    SansinchukKor,
    SansinchukMan,
    SasipgujechukKor,
    SasipgujechukMan,
    SinmyochukKor,
    SinmyochukMan,
    SosangchukKor,
    SosangchukMan,
    SumokjangbonganchukKor,
    SumokjangbonganchukMan,
    SungbokjechukKor,
    SungbokjechukMan,
    TojisinchukKor,
    TojisinchukMan
  },
  data() {
    var day = new Date()
    return {
      fontType:'gungseo',
      checkedLanguage: false,
      checkDirectWriteFlag: false,
      keyword: '',
      appellation: '孝子.효자', // 호칭
      deceasedName: '',
      ancestorName: '',
      firstAddress: '',
      secondAddress: '',
      placeName: '',
      hour: '',
      minute: '',
      deceasedAge: '',
      congratulatoryOfficerName: '', // 제주. 축관 이름
      tempPaperListName: '',
      paperListName: '顯考學生府君.현고학생부군.아버님', // 지방
      paperListNameKorean: '',
      surName: '', // 성
      tempCongratulationType: '',
      congratulationType: '',
      languageType: 'Man',
      selectedSurNameId: null,
      yearMonthDay: day.getFullYear() + '-' + (day.getMonth() + 1) + '-' + (day.getDate() < 10 ? '0' + day.getDate() : day.getDate())
    }
  },
  computed: {
    ...mapGetters(["getSurNameList"]),
  },
  mounted() {
    this.$nextTick(function() {
      $(".selector").flatpickr({
        dateFormat: "Y-m-d",
        
		    "locale": "ko",
        maxDate: new Date().fp_incr(30),
        disableMobile: "true"
      })
    })
  },
  beforeDestroy() {
    this.$store.commit('SUR_NAME_INIT')
  },
  methods: {
    chuckmoonPrint() {
      jQuery('#print_big').print();
    },
    initClick() {
      var day = new Date()
      this.checkedLanguage = false;
      this.checkDirectWriteFlag = false;
      this.appellation = '孝子.효자'; // 호칭
      this.deceasedName = '';
      this.ancestorName = '';
      this.firstAddress = '';
      this.secondAddress = '';
      this.placeName = '';
      this.hour = '';
      this.minute = '';
      this.deceasedAge = '';
      this.congratulatoryOfficerName = ''; // 제주. 축관 이름
      this.tempPaperListName = '',
      this.paperListName = '顯考學生府君.현고학생부군.아버님'; // 지방
      this.paperListNameKorean = '';
      this.surName = ''; // 성
      this.tempCongratulationType = '';
      this.congratulationType = '';
      this.languageType = 'Man';
      this.yearMonthDay = day.getFullYear() + '-' + (day.getMonth() + 1) + '-' + (day.getDate() < 10 ? '0' + day.getDate() : day.getDate());
      this.$store.commit('SUR_NAME_INIT');
    },
    changeLanguge() {
      this.checkedLanguage = !this.checkedLanguage
      this.languageType = !this.checkedLanguage ? 'Man' : 'Kor'
      this.checkDirectWriteFlag = false
    },
    checkDirectWrite() {
      this.checkDirectWriteFlag = !this.checkDirectWriteFlag
    },
    changeCongratulationType(type) {
      if (type === 'Chohon') {
        this.appellation = '공'
      } else {
        this.appellation = '孝子.효자'
      }
      this.congratulationType = type
    },
    changeAppellation(appellation) {
      this.appellation = appellation
    },
    changeParperListName(paperListName) {
      if (paperListName === '顯僻學生府君.현벽학생부군.남편') {
        this.appellation = '主婦.주부'
      } else if (paperListName === '亡室孺人.망실유인.아내') {
        this.appellation = '夫.부'
      } else if (paperListName === '顯兄學生府君.현형학생부군.형') {
        this.appellation = '弟.제'
      } else if (paperListName === '亡弟學生府君.망제학생부군.아우') {
        this.appellation = '兄.형'
      } else if (this.appellation === '主婦.주부' || this.appellation === '夫.부' || this.appellation === '弟.제' || this.appellation === '兄.형') {
        this.appellation = '孝子.효자'
      }
      this.paperListName = paperListName
      this.$store.commit('SUR_NAME_INIT')
    },
    isNeededCongratulatoryOfficerName() {
      return !(this.congratulationType === 'Nojechuk' && this.languageType === 'Kor') &&
        !(this.congratulationType === 'Chohon') &&
        !(this.congratulationType === 'Gyebinchuk') &&
        !(this.congratulationType === 'Jeonyukjesachuk') &&
        !(this.congratulationType === 'Balingosa') &&
        !(this.congratulationType === 'Chungugosa')
    },
    isNeededDeceasedName() {
      return (this.congratulationType === 'Balingosa' && this.languageType === 'Kor') ||
        (this.congratulationType === 'Dongkangsunyoungchuk' && this.languageType === 'Kor') ||
        (this.congratulationType === 'Nojechuk' && this.languageType === 'Kor')
    },
    isNeededAncestorName() {
      return (this.congratulationType === 'Dongkangsunyoungchuk' && this.languageType === 'Kor')
    },
    isNeededPlaceName() {
      return (this.congratulationType === 'Dongkangsunyoungchuk' && this.languageType === 'Kor')
    },
    isNeededCalendar() {
      return !(this.congratulationType === 'Gyebinchuk') &&
        !(this.congratulationType === 'Jeonyukjesachuk') &&
        !(this.congratulationType === 'Balingosa') &&
        !(this.congratulationType === 'Chungugosa')
    },
    isNeededSurName() {
      var paperListName = this.paperListName.split(".")
      return this.isNeededPaperListName() && (paperListName[2] === '어머님' || paperListName[2] === '할머님' ||
        paperListName[2] === '증조할머님' || paperListName[2] === '고조할머님' || paperListName[2] === '아내');
    },
    isNeededAppellation() {
      return (this.congratulationType === 'Pamyochuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Sungbokjechuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Pyungtochuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Banhonjechuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Daesangchuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Dangiltalgsangchuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Sosangchuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Samutalsangchuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Napgolmyobonganchuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Baekiltalsangchuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Sumokbanganchuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Dongkangsunyoungchuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Sangsinjechuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Jaewoochuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Hwajangjechuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Samwoojechuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Sasipgujechuk' && this.languageType === 'Man')
    },
    isNeededPaperListName() {
      return (this.congratulationType === 'Pamyochuk') || (this.congratulationType === 'Sungbokjechuk') ||
        (this.congratulationType === 'Pyungtochuk') || (this.congratulationType === 'Banhonjechuk') ||
        (this.congratulationType === 'Daesangchuk') || (this.congratulationType === 'Dangiltalgsangchuk') ||
        (this.congratulationType === 'Sosangchuk') || (this.congratulationType === 'Samutalsangchuk') ||
        (this.congratulationType === 'Napgolmyobonganchuk') || (this.congratulationType === 'Baekiltalsangchuk') ||
        (this.congratulationType === 'Sumokbanganchuk') || (this.congratulationType === 'Sangsinjechuk') ||
        (this.congratulationType === 'Jaewoochuk') || (this.congratulationType === 'Hwajangjechuk') ||
        (this.congratulationType === 'Samwoojechuk') || (this.congratulationType === 'Sasipgujechuk') ||
        (this.congratulationType === 'Dongkangsunyoungchuk' && this.languageType === 'Man') ||
        (this.congratulationType === 'Sansinchuk' && this.languageType === 'Kor') ||
        (this.congratulationType === 'Tojisinchuk' && this.languageType === 'Kor') ||
        (this.congratulationType === 'Gumyochuk' && this.languageType === 'Kor') ||
        (this.congratulationType === 'Sinmyochuk' && this.languageType === 'Kor')
    },
    isChohonCongratulationType() {
      return (this.congratulationType === 'Chohon')
    },
    isTargetCongratulation(congratulationType, languageType) {
      return this.congratulationType === congratulationType && this.languageType === languageType
    },
    getCongratulationProps() {
      if (!this.checkDirectWriteFlag) {
        this.paperListNameKorean = this.paperListName.split(".")[2]
      }
      var yearMonthDay = this.yearMonthDay.split("-")
      var ganjiDate = solarToLunar(yearMonthDay[0], yearMonthDay[1], yearMonthDay[2])
      const props = {
        appellation: this.appellation, // 호칭
        deceasedName: this.deceasedName,
        ancestorName: this.ancestorName,
        firstAddress: this.firstAddress,
        secondAddress: this.secondAddress,
        placeName: this.placeName,
        hour: this.hour,
        minute: this.minute,
        deceasedAge: this.deceasedAge,
        congratulatoryOfficerName: this.congratulatoryOfficerName, // 제주. 축관 이름
        paperListName: this.paperListName, // 지방
        paperListNameKorean: this.paperListNameKorean,
        surName: this.surName, // 성
        ganjiDate: ganjiDate,
        yearMonthDay: yearMonthDay
      }
      console.log('result: ', props)
      return props
    },
    searchSurName() {
      const parameter = {
        keyword: this.keyword
      }
      this.$store.dispatch('REQUEST_SUR_NAME_LIST', parameter)
    },
    selectSurName(surName) {
      this.selectedSurNameId = surName.id
      this.surName = surName.surNameMandarin + '.' + surName.surNameKorean
    },
    saveTablet() {
      
      const contentTxt = this.$refs.printWrapId.innerHTML

      const parameter = {
        tablet: {
          tablet2CheckedLanguage: this.checkedLanguage,
          tablet2CheckDirectWriteFlag:this.checkDirectWriteFlag ,
          tablet2Appellation:this.appellation,
          tablet2DeceasedName:this.deceasedName,
          tablet2AncestorName: this.ancestorName,
          tablet2FirstAddress:this.firstAddress,
          tablet2SecondAddress:this.secondAddress,
          tablet2PlaceName:this.placeName,
          tablet2Hour: this.hour,
          tablet2Minute:this.minute,
          tablet2DeceasedAge:this.deceasedAge,
          tablet2CongratulatoryOfficerName:this.congratulatoryOfficerName,
          tablet2TempPaperListName:this.tempPaperListName,
          tablet2PaperListName:this.paperListName,
          tablet2PaperListNameKorean:this.paperListNameKorean,
          tablet2SurName: this.surName,
          tablet2TempCongratulationType: this.tempCongratulationType,
          tablet2CongratulationType: this.congratulationType,
          tablet2LanguageType:this.languageType,

          tabletType: { codeId: 'TABLET-1' }
        }
      }
      this.$store.dispatch("REGIST_TABLET2", parameter);
    }
  }
}
</script>

